/* @import '~@csstools/normalize.css'; */

.description {
  margin-top: 30px;
  width: 600px;

  border-top-left-radius: 8px;
  border-top-right-radius: 8px;

  text-align: center;
  background-color: white;

  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}

.avatar {
  margin-top: 60px;
  margin-bottom: 60px;
  display: inline;
  max-width: 300px;
  max-height: 300px;
  border-radius: 50%;
  background-color: rgb(121, 179, 179);
}

.name {
  margin-bottom: 30px;

  font-weight: 700;
  font-size: 56px;
  line-height: 42px;
  text-align: center;
  letter-spacing: 0.03em;

  color: #212121;
}

.tag {
  margin-bottom: 40px;

  font-weight: 400;
  font-size: 36px;

  text-align: center;
  letter-spacing: 0.03em;

  color: gray;
}

.location {
  font-weight: 400;
  font-size: 36px;

  text-align: center;
  letter-spacing: 0.03em;

  color: gray;
}
